* {
  box-sizing: border-box;
}
body {
  font-family: "Montserrat", sans-serif;
  margin: 0;
}

.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: none;
}

/* 헤더 */
.main-header {
  padding: 8px 16px;
  width: 100%;
  top: 0;
  left: 0;
  position: fixed;
  background: #2ddf5c;
  z-index: 1;
}
.main-header > div {
  /*로고div*/
  display: inline-block;
  vertical-align: middle;
}
.main-header__brand {
  /*로고글씨*/
  color: #0e4f1f;
  text-decoration: none;
  font-weight: bold;
  font-size: 22px;
  height: 22px;
  display: inline-block;
}
.main-header__brand img {
  height: 100%;
}

.main-nav {
  /*네비게이션*/
  display: inline-block;
  text-align: right;
  width: calc(100% - 74px);
  vertical-align: middle;
}
.main-nav__items {
  /*네비게이션 ul*/
  margin: 0;
  padding: 0;
  list-style: none;
}
.main-nav__item {
  /*네비게이션 li들*/
  display: inline-block;
  margin: 0 16px;
}
.main-nav__item a {
  color: #0e4f1f;
  text-decoration: none;
  font-weight: bold;
  padding: 3px 0;
}
.main-nav__item a:hover,
.main-nav__item a:active {
  color: white;
  border-bottom: 5px solid white;
}
.main-nav__item--cta a {
  color: white;
  background: #ff1b68;
  padding: 8px 16px;
  border-radius: 8px;
}
.main-nav__item--cta a:hover,
.main-nav__item--cta a:active {
  color: #ff1b68;
  background: white;
  border: none;
}

/* 푸터 */
.main-footer {
  background: black;
  padding: 32px;
  margin-top: 48px;
}
.main-footer__links {
  list-style: none;
  margin: 0;
  padding: 0;
  text-align: center;
}
.main-footer__link {
  display: inline-block;
  margin: 0 16px;
}
.main-footer__link a {
  color: white;
  text-decoration: none;
}

.main-footer__link a:hover,
.main-footer__link a:active {
  color: #ccc;
}

.button {
  background: #0e4f1f;
  color: white;
  font: inherit;
  border: 1.5px solid #0e4f1f;
  padding: 8px;
  border-radius: 8px;
  font-weight: bold;
  cursor: pointer;
}
.button:hover,
.button:active {
  color: #0e4f1f;
  background: white;
}
.button:focus {
  outline: none;
}
